
































import Vue from "vue";
export default Vue.extend({
  model: {
    prop: "value",
    event: "change"
  },
  props: {
    value: {
      type: Number,
      default: 0
    },
    predefineRange: {
      type: Array,
      default: (): number[] => [
        10,
        20,
        25,
        50,
        75,
        100,
        110,
        125,
        150,
        175,
        200
      ]
    },
    showReset: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      resetVal: 100
    };
  },
  computed: {
    renderValue(): number {
      return this.predefineRange.includes(this.value) ? this.value : 100;
    }
  },
  methods: {
    handleMinus() {
      let index = this.predefineRange.findIndex(v => v === this.renderValue);
      index--;
      if (index < 0) return;
      this.$emit("change", this.predefineRange[index]);
    },
    handleAdd() {
      let index = this.predefineRange.findIndex(v => v === this.renderValue);
      index++;
      const max = this.predefineRange.length - 1;
      if (index > max) return;
      this.$emit("change", this.predefineRange[index]);
    },
    handleReset() {
      this.$emit("change", this.resetVal);
    }
  },
  created() {
    this.resetVal = this.value;
  }
});
