








































import Vue from "vue";
import { v4 as uuid } from "uuid";
import Watermark from "./core/watermarkCore";
import WatermarkEditor from "./components/editor.vue";
import WatermarkLayout from "./components/layout.vue";
import WatermarkHeader from "./components/subHeader.vue";
import WatermarkSlider from "./components/slider.vue";
import MainHeader from "@/components/mainHeader/index.vue";
import wZoom from "@/components/zoom.vue";
// import exampleImg from "./imgs/example.jpg";
import exampleImg from "@/assets/logo.png";
import "./style.scss";
interface File {
  name: string;
  uid: string;
  url: string;
  raw: any;
}

const example: File = {
  name: "example.jpg",
  uid: uuid(),
  url: exampleImg,
  raw: undefined
};
export default Vue.extend({
  name: "watermark",
  components: {
    MainHeader,
    WatermarkEditor,
    WatermarkLayout,
    WatermarkHeader,
    WatermarkSlider,
    wZoom
  },
  data() {
    return {
      watermark: null as Watermark | null,
      drawFileUid: example.uid as string | undefined,
      fileList: [example] as File[],
      option: {
        text: "仅用于办理XXXX，他用无效。",
        fontSize: 22,
        fontFamily: "黑体",
        fillStyle: "rgba(100, 100, 100, 0.4)",
        watermarkWidth: 280,
        watermarkHeight: 180,
        rotate: 20
      },
      zoom: 100
    };
  },
  computed: {
    file: {
      get(): File | undefined {
        return this.fileList.find(file => file.uid === this.drawFileUid);
      },
      set(file: File) {
        this.drawFileUid = file?.uid;
      }
    }
  },
  methods: {
    handleAddFile(file: File) {
      this.fileList.push(file);
      this.file = file;
      this.draw();
    },
    handlePreview(file: File) {
      this.file = file;
      this.draw();
    },
    handleRotate() {
      this.watermark?.rotate();
    },
    handleChange(val: any) {
      this.option = val;
      this.watermark?.setOption(val);
    },
    draw() {
      if (this.file) {
        this.watermark?.addImage(this.file.url, this.file.name).then(opt => {
          // const { canvasWidth, canvasHeight } = opt;
          console.log("draw params", opt);
        });
      } else this.watermark?.clear();
    },
    handleDelete(file: File) {
      this.$confirm(`确定移除 ${file.name}？`).then(val => {
        if (val === "confirm") {
          this.fileList = this.fileList?.filter(item => item !== file);
          this.draw();
        }
      });
    },
    async handleSave(type: string) {
      if (!this.fileList.length || type !== "all") {
        this.watermark?.save();
        return;
      }
      for (const file of this.fileList) {
        const { name, url } = file;
        await this.watermark?.addImage(url, name);
        await this.watermark?.setOption(this.option);
        this.watermark?.save();
      }
    }
  },
  mounted() {
    const ref = this.$refs.watermark as HTMLElement;
    this.watermark = new Watermark({ ref });

    this.file && this.watermark?.addImage(this.file.url, this.file.name);
  },
  destroyed() {
    this.watermark?.destroyed();
  },
  watch: {
    option: {
      handler(val) {
        this.handleChange(val);
      },
      deep: true
    },
    zoom(val) {
      this.watermark?.zoom(val);
    }
  }
});
