





















import Vue from "vue";
import { v4 as uuid } from "uuid";
import { getModule } from "vuex-module-decorators";
import UserStore from "@/store/modules/userCenter";
import MainHeaderEditor from "./mainHeaderEditor.vue";
export default Vue.extend({
  components: { MainHeaderEditor },
  computed: {
    $UserStore() {
      return getModule(UserStore, this.$store);
    },
    isLogin(): boolean {
      return this.$UserStore._isLogin;
    }
  },
  methods: {
    toqueryStr(queryObj: { [name: string]: string }) {
      return Object.keys(queryObj)
        .reduce((arr: string[], key: string) => {
          if (queryObj[key]) {
            arr.push(`${key}=${queryObj[key]}`);
          }
          return arr;
        }, [])
        .join("&");
    },
    buildWeChartQrcodeUrl() {
      // https://developers.weixin.qq.com/doc/oplatform/Website_App/WeChat_Login/Wechat_Login.html
      const baseURL = " https://open.weixin.qq.com/connect/qrconnect";
      const params = {
        appid: "wxb665795cfaf33cf7",
        // eslint-disable-next-line @typescript-eslint/camelcase
        redirect_uri: encodeURI(""),
        // eslint-disable-next-line @typescript-eslint/camelcase
        response_type: "code",
        scope: "snsapi_login",
        state: uuid()
      };
      return `${baseURL}?${this.toqueryStr(params)}`;
    },
    handleLogin() {
      window.open(this.buildWeChartQrcodeUrl());
    }
  }
});
