












































































import Vue from "vue";
import { getModule } from "vuex-module-decorators";
import WatermarkStore from "@/store/modules/watermark";
import { fontsFamilies, fontSizes, predefineColors } from "../constants";
import WatermarkUpload from "./upload.vue";
export default Vue.extend({
  components: { WatermarkUpload },
  props: {
    formData: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      fontFamilyOption: fontsFamilies.map(value => ({ lable: value, value })),
      fontSizeOption: fontSizes.map(value => ({ label: `${value}px`, value })),
      predefineColors: predefineColors
    };
  },
  computed: {
    $WatermarkStore(): WatermarkStore {
      return getModule(WatermarkStore, this.$store);
    },
    activeName(): string {
      return this.$WatermarkStore._mainHeaderActiveName;
    }
  },
  methods: {}
});
