import Canvas2Image from "./canvas2image";
import {
  Props,
  Option,
  UserOption,
  SubProps,
  SubOption
} from "./WatermarkCore.d";

class CommonMethods {
  public createCanvasElement(width: number, height: number): HTMLCanvasElement {
    const canvas = document.createElement("canvas");
    canvas.setAttribute("width", this.convertPX(width));
    canvas.setAttribute("height", this.convertPX(height));
    // canvas.style.width = "100%";
    // canvas.style.height = "100%";
    return canvas;
  }
  /**
   * 数字转像素
   * @param val
   */
  public convertPX(val: number | string): string {
    if (typeof val === "number") {
      return `${val}px`;
    } else if (typeof val === "string") {
      return /px^/.test(val) ? val : `${val}px`;
    } else {
      throw new Error(`${val} 类型错误`);
    }
  }

  public parseIntPlus(val: number | string, type = 10) {
    return parseInt(String(val), type);
  }

  /**
   * 角度转换弧度
   * @param {Angle} angle
   * @returns {Rad}
   */
  public convertRad(angle: number): number {
    return (Math.PI / 180) * angle;
  }

  public convertDataURL(file: File, type?: string): Promise<string> {
    return new Promise(resolve => {
      const reader = new FileReader();
      if (!type || type === "dataURL") reader.readAsDataURL(file);
      else reader.readAsText(file);
      reader.onload = () => {
        resolve(reader.result as string);
      };
    });
  }
}

class SubMark extends CommonMethods {
  static minWidth = 50;
  static minHeight = 50;
  static minFontSize = 10;
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;
  constructor(props: SubProps) {
    super();
    const { canvas, ctx } = this.create(props);
    this.canvas = canvas;
    this.ctx = ctx;
  }
  get canvasWidth() {
    return this.canvas.width;
  }
  set canvasWidth(value) {
    this.canvas.width = Math.max(value, SubMark.minWidth);
  }
  get canvasHeight() {
    return this.canvas.height;
  }
  set canvasHeight(value) {
    const h = Math.max(value, SubMark.minHeight);
    this.canvas.height = this.getRealWidth(this.canvasWidth, h);
  }
  private getRealWidth(width: number, height: number) {
    const { sqrt, pow } = Math;
    return sqrt(pow(width, 2) + pow(height, 2));
  }
  private create(props: SubProps) {
    const { width, height } = props;
    const canvas = this.createCanvasElement(width, height);
    const ctx = canvas.getContext("2d");
    if (!(ctx instanceof CanvasRenderingContext2D))
      throw new Error("ctx is not defined");

    canvas.width = this.getRealWidth(width, height);
    canvas.height = width;
    return {
      canvas,
      ctx
    };
  }
  private canvasTextAutoLine(
    ctx: CanvasRenderingContext2D,
    width: number,
    str: string,
    initX: number,
    initY: number,
    lineHeight = 20
  ) {
    // ctx.fillText(str, 0, lineHeight);
    // return;
    let lineWidth = 0;
    const canvasWidth = width;
    let lastSubStrIndex = 0;
    for (let i = 0; i < str.length; i++) {
      lineWidth += ctx.measureText(str[i]).width;

      if (lineWidth > canvasWidth - initX) {
        //减去initX,防止边界出现的问题
        ctx.fillText(str.substring(lastSubStrIndex, i), initX, initY);
        initY += lineHeight;
        lineWidth = 0;
        lastSubStrIndex = i;
      }
      if (i === str.length - 1) {
        ctx.fillText(str.substring(lastSubStrIndex, i + 1), initX, initY);
      }
    }
  }
  private renderText(option: SubOption) {
    this.clearCanvas();
    const { text, rotate, fillStyle, fontFamily } = option;
    const fontSize = Math.max(option.fontSize, SubMark.minFontSize);
    const { sin, tan } = Math;
    this.ctx.font = `${fontSize}px ${fontFamily}`;

    //文字倾斜角度
    this.ctx.rotate((-rotate * Math.PI) / 180);
    this.ctx.fillStyle = fillStyle;
    const Y = this.parseIntPlus(
      sin((rotate * Math.PI) / 180) * this.canvasWidth
    );
    const X = -this.parseIntPlus(Y / tan(((90 - rotate) * Math.PI) / 180));
    this.canvasTextAutoLine(
      this.ctx,
      this.canvasWidth,
      text,
      X + 10,
      Y + Number(fontSize) + 20,
      Number(fontSize) * 1.4
    );
  }
  // 清空画布
  private clearCanvas() {
    this.ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
  }
  // 渲染水印
  public drawWatermark(
    ctx: CanvasRenderingContext2D,
    width: number,
    height: number,
    option: SubOption
  ) {
    const { width: w, height: h, fontSize } = option;
    // 重置水印高宽
    if (w && w !== this.canvasWidth) this.canvasWidth = w;
    if (h && h !== this.canvasHeight) this.canvasHeight = h;

    option.fontSize = fontSize < 10 ? 10 : this.parseIntPlus(fontSize);

    // 绘制水印
    this.renderText(option);
    //平铺--重复小块的canvas
    const pat = ctx.createPattern(this.canvas, "repeat");
    if (!pat) return;
    ctx.fillStyle = pat;
    ctx.fillRect(0, 0, width, height);
  }
}

class Watermark extends CommonMethods {
  private ref: HTMLElement;
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;
  private userOption: UserOption;
  private rotateType: number;
  private watermark: SubMark;
  private timer: number | undefined = undefined;
  private fileName: string | undefined = undefined;

  private img?: HTMLImageElement;
  // 图形最大宽度
  static imgMax = 2000;
  static defaultUserOpt = {
    text: "仅用于办理XXXX，他用无效。",
    fontSize: 23,
    fontFamily: "黑体",
    fillStyle: "rgba(100, 100, 100, 0.4)",
    watermarkWidth: 280,
    watermarkHeight: 180,
    rotate: 20 // 文字旋转角度
  };
  /**
   *
   * @param props
   */
  constructor(props: Props) {
    super();
    const { ref, height = 100, width = 100, dataURL } = props;
    this.ref = ref;
    this.userOption = Object.assign({}, Watermark.defaultUserOpt);
    this.rotateType = 0;
    this.validate(props);
    const { canvas, ctx } = this.create(width, height);
    this.watermark = new SubMark({ width: 100, height: 50 });
    this.canvas = canvas;
    this.ctx = ctx;
    this.setStyle();
    dataURL && this.addImage(dataURL);
  }
  get canvasWidth() {
    return this.canvas ? this.canvas.width : 0;
  }
  set canvasWidth(val: number) {
    this.canvas.width = val;
    // this.ref.style.width = `${val}px`;
  }
  get canvasHeight() {
    return this.canvas ? this.canvas.height : 0;
  }
  set canvasHeight(val: number) {
    this.canvas.height = val;
    // this.ref.style.height = `${val}px`;
  }
  get imgWidth() {
    if (!this.img) return 0;
    const { width } = this.img;
    return width > Watermark.imgMax ? Watermark.imgMax : width;
  }
  get imgHeight() {
    if (!this.img) return 0;
    const { width, height } = this.img;
    return width > Watermark.imgMax
      ? (Watermark.imgMax * height) / width
      : height;
  }
  get renderOption() {
    const { watermarkWidth, watermarkHeight, ...rest } = this.userOption;
    return {
      width: watermarkWidth,
      height: watermarkHeight,
      ...rest
    };
  }
  private validate({ ref }: Props) {
    if (!(ref instanceof HTMLElement)) {
      throw new Error(`ref ${ref} 不是HTMLElement`);
    }
    return true;
  }
  private setStyle() {
    this.ref &&
      Object.assign(this.ref.style, {
        'text-align': 'center'
      });
    this.canvas &&
      Object.assign(this.canvas.style, {
        // "transform-origin": "50% 50%"
        "transform-origin": "0 0"
      });
  }
  private create(
    width: number,
    height: number
  ): {
    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;
  } {
    const canvas = this.createCanvasElement(width, height);
    if (!(canvas instanceof HTMLCanvasElement))
      throw new Error("canvas is not defined");
    const ctx = canvas.getContext("2d");
    if (!(ctx instanceof CanvasRenderingContext2D))
      throw new Error("ctx is not defined");
    this.ref.appendChild(canvas);
    return {
      canvas,
      ctx
    };
  }
  /***** draw start *****/
  // 渲染图片
  private drawImage() {
    if (!this.img) return;
    const img = this.img,
      imgWidth = this.imgWidth,
      imgHeight = this.imgHeight;
    switch (this.rotateType) {
      case 0:
      default:
        this.canvasWidth = imgWidth;
        this.canvasHeight = imgHeight;
        this.ctx.drawImage(img, 0, 0, imgWidth, imgHeight);
        break;
      case 1:
        this.canvasWidth = imgHeight;
        this.canvasHeight = imgWidth;
        this.ctx.save();
        this.ctx.rotate((90 * Math.PI) / 180);
        this.ctx.drawImage(img, 0, -imgHeight, imgWidth, imgHeight);
        this.ctx.restore();
        break;
      case 2:
        this.canvasWidth = imgWidth;
        this.canvasHeight = imgHeight;
        this.ctx.save();
        this.ctx.rotate((180 * Math.PI) / 180);
        this.ctx.drawImage(img, -imgWidth, -imgHeight, imgWidth, imgHeight);
        this.ctx.restore();
        break;
      case 3:
        this.canvasWidth = imgHeight;
        this.canvasHeight = imgWidth;
        this.ctx.save();
        this.ctx.rotate((270 * Math.PI) / 180);
        this.ctx.drawImage(img, -imgWidth, 0, imgWidth, imgHeight);
        this.ctx.restore();
        break;
    }
  }
  private _draw() {
    this.clear();
    this.drawImage();
    this.watermark.drawWatermark(
      this.ctx,
      this.canvasWidth,
      this.canvasHeight,
      this.renderOption
    );
  }
  // 绘制入口
  private draw() {
    return new Promise(resolve => {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this._draw();
        console.log("drawing successful");
        const params = {
          canvasWidth: this.canvasWidth,
          canvasHeight: this.canvasHeight
        };
        resolve(params);
      });
    });
  }
  /***** draw end *****/
  private loadImage(dataURL: string): Promise<HTMLImageElement> {
    const img = new Image();
    img.src = dataURL;
    return new Promise((resolve, reject) => {
      img.onload = () => {
        resolve(img);
      };
      img.onerror = err => {
        reject(err);
      };
    });
  }

  /**
   * 添加图片
   * @param dataURL
   */
  public async addImage(dataURL: string | File, fileName?: string) {
    if (typeof dataURL !== "string") {
      dataURL = await this.convertDataURL(dataURL);
    }
    this.img = await this.loadImage(dataURL);
    this.fileName = fileName;
    return this.draw();
  }
  // 旋转图形
  public rotate(type?: number) {
    typeof type === "number" ? (this.rotateType = type) : this.rotateType++;
    this.rotateType %= 4;
    return this.draw();
  }
  /**
   * zoom
   */
  public zoom(percent: number) {
    const percentX = percent / 100;
    const percentY = percent / 100;
    Object.assign(this.canvas.style, {
      width: this.canvasWidth * percentX + "px",
      height: this.canvasHeight * percentY + "px"
    });
    // this.canvas.style.transform = `scale(${percentX},${percentY})`;
    // Object.assign(this.ref.style, {
    //   width: this.canvasWidth * percentX + 'px',
    //   height: this.canvasHeight * percentY + 'px'
    // })
  }
  public save() {
    this.canvas && Canvas2Image.saveAsJPEG(this.canvas, this.fileName);
  }
  /**
   *
   * @param option
   */
  public setOption(option: Option) {
    const { rotateType, ...rest } = option;
    Object.assign(this.userOption, rest);
    typeof rotateType === "number" && this.rotate(rotateType);
    return this.draw();
  }
  // 清空画布
  public clear() {
    this.ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
  }
  destroyed() {}
}

export default Watermark;
