










import Vue from "vue";
import { getModule } from "vuex-module-decorators";
import WatermarkStore from "@/store/modules/watermark";
export default Vue.extend({
  data() {
    return {};
  },
  computed: {
    $WatermarkStore(): WatermarkStore {
      return getModule(WatermarkStore, this.$store);
    },
    activeName: {
      get(): string {
        return this.$WatermarkStore._mainHeaderActiveName;
      },
      set(mainHeaderActiveName: string): void {
        this.$WatermarkStore.UPDATE_INFO({ mainHeaderActiveName });
      }
    }
  },
  methods: {
    handleClick() {}
  }
});
