import { v4 as uuid } from "uuid";

interface Option {
  type?: string;
  name?: string;
}
class Canvas2image {
  static QUALITY_RATIO = 0.95;
  private downloadFile(dataURL: string, name: string) {
    //   生产一个a元素
    const a = document.createElement("a");
    // 创建一个单击事件
    const event = new MouseEvent("click");
    // 将a的download属性设置为我们想要下载的图片名称，若name不存在则使用‘下载图片名称’作为默认名称
    a.download = name || uuid();
    // 将生成的URL设置为a.href属性
    a.href = dataURL;
    // 触发a的单击事件
    a.dispatchEvent(event);
  }
  private canvasToDataURL(canvas: HTMLCanvasElement, suffix: string) {
    return canvas.toDataURL(`image/${suffix}`, Canvas2image.QUALITY_RATIO);
  }
  private save(canvas: HTMLCanvasElement, option: Option) {
    const { name = uuid(), type = "jpeg" } = option;
    const dataURL = this.canvasToDataURL(canvas, type);
    this.downloadFile(dataURL, name);
  }
  public saveAsJPEG(canvas: HTMLCanvasElement, name?: string) {
    this.save(canvas, { name, type: "jpeg" });
  }
  public saveAsPNG(canvas: HTMLCanvasElement, name?: string) {
    this.save(canvas, { name, type: "png" });
  }
}

export default new Canvas2image();
